import { auth, provider, db } from "./components/config";
import { ref, set, push, onValue, update } from "firebase/database";
import { signInWithPopup } from 'firebase/auth';
import React, { useRef } from 'react';
import { Button, Input } from "antd";

function App() {
  const uid = useRef(null);
  
  function writeUIdToDatabase(uid) {
    console.log("UID : " + uid);
    update(ref(db, 'UIDs'), {
      [uid]: uid
    });
  }

  function handleClick() {
    signInWithPopup(auth, provider).then(data => {
      console.log(data);
      writeUIdToDatabase(data.user.providerData[0].uid);
      uid.current.input.value = data.user.providerData[0].uid;
    })
  }

  const contentStyle = {
    left: "50%",
    transform:"translate(-50%,-50%)",
    display:"inline-block"
  }

  return (
    <div className="App" style={{height: "100vh"}}>
      <div style={{position:"absolute", top:"50%", left:"50%", transform:"translate(-50%,-50%)"}}>
        <Input placeholder="UID of Google Account" disabled={true} style={contentStyle} ref={uid}></Input>
        <Button type="primary" shape="round" onClick={() => handleClick()} style={{...contentStyle, top:"10px"}}>Google Sign-in</Button>
      </div> 
    </div>
  );
}

export default App;
